import { FadeInUp, ScaleIn } from './animations'

export default function Hero() {
  return (
    <section className="relative bg-gradient-to-br from-gray-900 via-gray-800 to-gray-900 py-24 px-4 overflow-hidden min-h-screen flex items-center">
      {/* Advanced Background Pattern */}
      <div className="absolute inset-0 opacity-40">
        <div className="absolute inset-0 bg-gradient-to-br from-gray-800 via-gray-900 to-black"></div>
        <div className="absolute inset-0 bg-[radial-gradient(circle_at_2px_2px,rgba(255,255,255,0.08)_1px,transparent_0)] bg-[length:40px_40px]"></div>
        <div className="absolute top-0 left-0 w-full h-full bg-gradient-to-br from-red-500/5 via-transparent to-blue-500/5"></div>
      </div>

      {/* Floating Elements */}
      <div className="absolute top-20 left-10 w-20 h-20 bg-red-500/10 rounded-full blur-xl animate-pulse"></div>
      <div className="absolute bottom-20 right-10 w-32 h-32 bg-blue-500/10 rounded-full blur-xl animate-pulse delay-1000"></div>
      <div className="absolute top-1/2 left-1/4 w-16 h-16 bg-purple-500/10 rounded-full blur-xl animate-pulse delay-500"></div>

      <div className="max-w-7xl mx-auto text-center relative z-10 w-full">
        {/* Modern Hero Card */}
        <div className="bg-white/5 backdrop-blur-lg rounded-3xl border border-white/10 shadow-2xl p-8 md:p-12 mb-12 hover:bg-white/10 transition-all duration-500">
          <div className="mb-12">
            <ScaleIn delay={200} className="relative inline-block mb-8">
              <div className="relative">
                <img
                  src="/Hussien.JPG"
                  alt="حسين"
                  className="w-40 h-40 rounded-full mx-auto object-cover border-4 border-white/20 shadow-2xl hover:scale-105 transition-all duration-500 ring-4 ring-red-500/20 hover:ring-red-500/40"
                />
                {/* Modern Avatar Glow */}
                <div className="absolute inset-0 rounded-full bg-gradient-to-r from-red-500/30 via-purple-500/30 to-blue-500/30 blur-xl opacity-50 animate-pulse"></div>
                <div className="absolute inset-2 rounded-full bg-gradient-to-r from-red-400/20 to-purple-400/20"></div>
              </div>
              {/* Status Indicator */}
              <div className="absolute bottom-2 right-2 w-6 h-6 bg-green-400 rounded-full border-4 border-gray-900 shadow-lg animate-pulse"></div>
            </ScaleIn>

            <div className="space-y-6 mb-10">
              <FadeInUp delay={400}>
                <h1 className="text-5xl md:text-7xl lg:text-8xl font-bold leading-tight">
                  <span className="bg-gradient-to-r from-white via-gray-100 to-gray-200 bg-clip-text text-transparent">
                    Hi, I&apos;m{' '}
                  </span>
                  <span className="bg-gradient-to-r from-red-400 via-red-300 to-red-500 bg-clip-text text-transparent animate-pulse">
                    Hussein
                  </span>
                </h1>
              </FadeInUp>

              <FadeInUp delay={600}>
                <p className="text-xl md:text-2xl text-gray-300 max-w-4xl mx-auto leading-relaxed font-light">
                  A passionate web developer specializing in creating{' '}
                  <span className="font-semibold text-white bg-gradient-to-r from-red-400 to-red-300 bg-clip-text text-transparent">
                    exceptional digital experiences
                  </span>{' '}
                  and innovative solutions
                </p>
              </FadeInUp>

              <FadeInUp delay={800}>
                <div className="flex flex-wrap justify-center gap-4 text-sm text-gray-400">
                  <span className="px-4 py-2 bg-red-500/10 rounded-full border border-red-500/20 backdrop-blur-sm">
                    🎯 Alpha Team Leader
                  </span>
                  <span className="px-4 py-2 bg-blue-500/10 rounded-full border border-blue-500/20 backdrop-blur-sm">
                    🚀 Full Stack Developer
                  </span>
                  <span className="px-4 py-2 bg-purple-500/10 rounded-full border border-purple-500/20 backdrop-blur-sm">
                    💡 Problem Solver
                  </span>
                </div>
              </FadeInUp>
            </div>
          </div>

          {/* Modern CTA Buttons */}
          <FadeInUp delay={1000} className="flex flex-col sm:flex-row gap-6 justify-center mb-12">
            <a
              href="/projects"
              className="group relative overflow-hidden bg-gradient-to-r from-red-600 via-red-500 to-red-600 text-white px-12 py-5 rounded-2xl font-semibold text-lg transition-all duration-500 shadow-2xl hover:shadow-red-500/25 transform hover:-translate-y-2 hover:scale-105"
            >
              <span className="relative z-10 flex items-center gap-3">
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 11H5m14 0a2 2 0 012 2v6a2 2 0 01-2 2H5a2 2 0 01-2-2v-6a2 2 0 012-2m14 0V9a2 2 0 00-2-2M5 11V9a2 2 0 012-2m0 0V5a2 2 0 012-2h6a2 2 0 012 2v2M7 7h10" />
                </svg>
                View My Work
              </span>
              <div className="absolute inset-0 bg-gradient-to-r from-red-500 to-red-600 opacity-0 group-hover:opacity-100 transition-opacity duration-500"></div>
              <div className="absolute inset-0 bg-white/10 transform -skew-x-12 -translate-x-full group-hover:translate-x-full transition-transform duration-700"></div>
            </a>

            <a
              href="/contact"
              className="group relative overflow-hidden border-2 border-red-500/50 text-red-300 px-12 py-5 rounded-2xl font-semibold text-lg hover:border-red-400 hover:text-red-200 transition-all duration-500 shadow-xl hover:shadow-red-500/10 transform hover:-translate-y-2 hover:scale-105 bg-gray-800/50 backdrop-blur-sm"
            >
              <span className="relative z-10 flex items-center gap-3">
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 8l7.89 4.26a2 2 0 002.22 0L21 8M5 19h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z" />
                </svg>
                Contact Me
              </span>
              <div className="absolute inset-0 bg-red-500/5 opacity-0 group-hover:opacity-100 transition-opacity duration-500"></div>
            </a>
          </FadeInUp>
        </div>

        {/* Modern Social Links */}
        <FadeInUp delay={1200} className="flex justify-center">
          <div className="flex gap-4 p-4 bg-white/5 backdrop-blur-lg rounded-2xl border border-white/10 shadow-xl">
            {[
              {
                href: 'https://github.com',
                icon: 'M12 0c-6.626 0-12 5.373-12 12 0 5.302 3.438 9.8 8.207 11.387.599.111.793-.261.793-.577v-2.234c-3.338.726-4.033-1.416-4.033-1.416-.546-1.387-1.333-1.756-1.333-1.756-1.089-.745.083-.729.083-.729 1.205.084 1.839 1.237 1.839 1.237 1.07 1.834 2.807 1.304 3.492.997.107-.775.418-1.305.762-1.604-2.665-.305-5.467-1.334-5.467-5.931 0-1.311.469-2.381 1.236-3.221-.124-.303-.535-1.524.117-3.176 0 0 1.008-.322 3.301 1.23.957-.266 1.983-.399 3.003-.404 1.02.005 2.047.138 3.006.404 2.291-1.552 3.297-1.23 3.297-1.23.653 1.653.242 2.874.118 3.176.77.84 1.235 1.911 1.235 3.221 0 4.609-2.807 5.624-5.479 5.921.43.372.823 1.102.823 2.222v3.293c0 .319.192.694.801.576 4.765-1.589 8.199-6.086 8.199-11.386 0-6.627-5.373-12-12-12z',
                label: 'GitHub',
                color: 'hover:text-gray-300'
              },
              {
                href: 'https://linkedin.com',
                icon: 'M20.447 20.452h-3.554v-5.569c0-1.328-.027-3.037-1.852-3.037-1.853 0-2.136 1.445-2.136 2.939v5.667H9.351V9h3.414v1.561h.046c.477-.9 1.637-1.85 3.37-1.85 3.601 0 4.267 2.37 4.267 5.455v6.286zM5.337 7.433c-1.144 0-2.063-.926-2.063-2.065 0-1.138.92-2.063 2.063-2.063 1.14 0 2.064.925 2.064 2.063 0 1.139-.925 2.065-2.064 2.065zm1.782 13.019H3.555V9h3.564v11.452zM22.225 0H1.771C.792 0 0 .774 0 1.729v20.542C0 23.227.792 24 1.771 24h20.451C23.2 24 24 23.227 24 22.271V1.729C24 .774 23.2 0 22.222 0h.003z',
                label: 'LinkedIn',
                color: 'hover:text-blue-400'
              },
              {
                href: 'mailto:hussein@example.com',
                icon: 'M3 8l7.89 4.26a2 2 0 002.22 0L21 8M5 19h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z',
                label: 'Email',
                color: 'hover:text-red-400'
              }
            ].map((social, index) => (
              <ScaleIn key={index} delay={1200 + (index * 100)} className="group relative p-4 bg-white/5 backdrop-blur-sm rounded-xl border border-white/10 shadow-lg hover:shadow-xl transition-all duration-500 hover:scale-110 hover:-translate-y-1">
                <svg className={`w-6 h-6 text-gray-400 ${social.color} transition-all duration-300`} fill="currentColor" viewBox="0 0 24 24">
                  <path d={social.icon}/>
                </svg>
                <div className="absolute inset-0 bg-gradient-to-r from-red-500/10 to-blue-500/10 rounded-xl opacity-0 group-hover:opacity-100 transition-opacity duration-500"></div>
                {/* Tooltip */}
                <div className="absolute bottom-full mb-2 left-1/2 transform -translate-x-1/2 px-3 py-1 bg-gray-900 text-white text-sm rounded-lg opacity-0 group-hover:opacity-100 transition-opacity duration-300 whitespace-nowrap border border-gray-700">
                  {social.label}
                  <div className="absolute top-full left-1/2 transform -translate-x-1/2 border-4 border-transparent border-t-gray-900"></div>
                </div>
              </ScaleIn>
            ))}
          </div>
        </FadeInUp>

        {/* Scroll Indicator */}
        <FadeInUp delay={1400} className="absolute bottom-8 left-1/2 transform -translate-x-1/2">
          <div className="animate-bounce">
            <svg className="w-6 h-6 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 14l-7 7m0 0l-7-7m7 7V3" />
            </svg>
          </div>
        </FadeInUp>
      </div>
    </section>
  )
}
